 palabras")]),_c('figure',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("@/assets/curso/tema3/11.svg"),"alt":"Ilustración comprensión palabras"}})]),_c('h5',{staticClass:"mb-3"},[_vm._v("¿Qué es la comprensión de palabras?")]),_c('p',{staticClass:"mb-4"},[_vm._v("Es la base fundamental para comprender cualquier texto.")]),_c('h5',{staticClass:"mb-3"},[_vm._v("¿Qué puedo hacer cuando me encuentro con palabras desconocidas?")]),_c('p',{staticClass:"mb-0"},[_vm._v("Puedes recurrir a diferentes estrategias que te permitan aproximarte a su significado sin necesidad de interrumpir la lectura para consultar un diccionario.")])])]),_c('div',{staticClass:"col-lg-6"},[_c('div',{staticClass:"tarjeta bg-8 p-4 p-md-5 h-100 text-center"},[_c('h4',{staticClass:"text-center mb-4"},[_vm._v("Comprensión de palabras")]),_c('h4',{staticClass:"text-center mb-4"},[_vm._v("Comprensión de oraciones")]),_c('figure',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_c('img',{staticStyle:{"width":"100%"},attrs:{"src":require("@/assets/curso/tema3/12.svg"),"alt":"Ilustración comprensión oraciones"}})]),_c('h5',{staticClass:"mb-3"},[_vm._v("¿Qué es una oración?")]),_c('p',{staticClass:"mb-4"},[_vm._v("La unidad de sentido más pequeña y completa para comunicar ideas.")]),_c('h5',{staticClass:"mb-3"},[_vm._v("¿Qué se expresa mediante las oraciones?")]),_c('p',{staticClass:"mb-0"},[_vm._v("Hechos, deseos, opiniones, dudas, preguntas, órdenes, suposiciones, sorpresas y asombros.")])])])])])]),_c('p',{staticClass:"mt-4"},[_vm._v("Identificar la intencionalidad de una oración es clave para comprender su significado. Para ello, se pueden aplicar las siguientes estrategias:")]),_c('div',{staticClass:"row justify-content-center"},[_c('div',{staticClass:"col-lg-10"},[_c('div',{staticClass:"titulo-sexto color-acento-contenido mb-3 mt-4"},[_c('h5',[_vm._v("Figura 2.")]),_c('span',[_vm._v("Estrategias de decodificación primaria según la t